import { makeStyles } from '@mui/styles'
import React from 'react'
import GroupBaners from '../components/home/GroupBaners';
import IconsSection from '../components/home/IconsSection';
import MainBaner from '../components/home/MainBaner';
import ProductLIstOne from '../components/home/ProductLIstOne';
import ProductLIstTwo from '../components/home/ProductLIsTwo';


const useStyles = makeStyles((theme)=>({
    homeMainContainer:{
        marginTop:"100px",
    },

    // @media
    [theme.breakpoints.down("md")]:{
        homeMainContainer:{
            marginTop:"51px",
        },
    }
}))

function Home() {
    const classes = useStyles();
    return (
        <main className={classes.homeMainContainer}>
            <MainBaner/>
            <GroupBaners/>
            <ProductLIstOne/>
            <IconsSection/>
            <ProductLIstTwo/>
        </main>
    )
}

export default Home
