import { makeStyles } from '@mui/styles'
import React from 'react'

import LocalShippingIcon from '@mui/icons-material/LocalShipping';
import AttachMoneyIcon from '@mui/icons-material/AttachMoney';
import MapIcon from '@mui/icons-material/Map';
import RotateLeftIcon from '@mui/icons-material/RotateLeft';
import VerifiedUserIcon from '@mui/icons-material/VerifiedUser';
import PhoneInTalkIcon from '@mui/icons-material/PhoneInTalk';

const useStyles = makeStyles((theme)=>({
    container:{
        margin: "0 auto",
        maxWidth: "1300px"
    },
    iconsContainer:{
        display: "flex",
        gridGap: "20px",
        margin: "10px auto",
        alignItems: "center",
        justifyContent: "center",
    },
    threeCol:{
        display: "flex",
    },
    threeColOne:{
        marginLeft: "-12px",
    },
    threeColTwo:{
        marginRight: "-12px",
        '& :hover':{

        }
    },
    iconBox:{
        display: "flex",
        flexDirection: "column!important",
        justifyContent: "center!important",
        alignItems: "center",
        color: "black",
        padding: "15px",
    },
    Icon:{
        padding: "15px",
        borderRadius: "50%",
        fontSize: "55px!important",
    },
    iconTitle:{
        marginTop: "10px",
        fontSize: "12px",
        color: "black",
        position: "relative",
        "&:hover":{
            fontSize: "13px"
        }
    },
    IconColorOne:{
        color: theme.palette.btnOne.main,
        border: `1px solid${theme.palette.btnOne.main}!important`,
        "&:hover":{
            background: theme.palette.btnOne.main,
            color: "#fff",
        }
    },
    IconColorTwo:{
        color: theme.palette.btnTwo.main,
         border: `1px solid${theme.palette.btnTwo.main}!important`,
         "&:hover":{
            background: theme.palette.btnTwo.main,
            color: "#fff",
        }
    },
    IconColorThree:{
        color: theme.palette.btnThree.main,
        border: `1px solid${theme.palette.btnThree.main}!important`,
        "&:hover":{
           background: theme.palette.btnThree.main,
           color: "#fff",
       }
    },
    IconColorFour:{
        color: theme.palette.btnFour.main,
        border: `1px solid${theme.palette.btnFour.main}!important`,
        "&:hover":{
           background: theme.palette.btnFour.main,
           color: "#fff",
       }
    },
    IconColorFive:{
        color: theme.palette.btnOne.main,
        border: `1px solid${theme.palette.btnOne.main}!important`,
        "&:hover":{
           background: theme.palette.btnOne.main,
           color: "#fff",
       }
    },
    IconColorSix:{
        color: theme.palette.btnTwo.main,
         border: `1px solid${theme.palette.btnTwo.main}!important`,
         "&:hover":{
            background: theme.palette.btnTwo.main,
            color: "#fff",
        }
    },
    afterTitle:{
        marginTop: "5px",
        width: "20px"
    },

    // @media
    [theme.breakpoints.down('sm')]:{
        iconsContainer:{
            display: "flex",
            flexDirection: "column",
        },
        Icon:{
            padding: "15px",
            fontSize: "50px!important",
        },
        iconTitle:{
            marginTop: "10px",
            fontSize: "11px",
        },
        threeColOne:{
            marginLeft: "1px",
        },
        threeColTwo:{
            marginRight: "1px",
        },
    }
}))

function IconsSection() {
    const classes = useStyles();
    return (
        <div className={classes.container}>
            <div className={classes.iconsContainer}>
                <div className={`${classes.threeCol} ${classes.threeColOne}`}>
                    <div className={classes.iconBox}>
                        <LocalShippingIcon className={`${classes.Icon} ${classes.IconColorOne}`} />
                        <div className={classes.iconTitle}>
                            <h5>تحویل سریع</h5>
                        </div>
                        <div className={`${classes.afterTitle} ${classes.IconColorOne}`}></div>
                    </div>
                    <div className={classes.iconBox}>
                        <AttachMoneyIcon className={`${classes.Icon} ${classes.IconColorTwo}`} />
                        <div className={classes.iconTitle}>
                            <h5>پایین ترین قیمت</h5>
                        </div>
                        <div className={`${classes.afterTitle} ${classes.IconColorTwo}`}></div>
                    </div>
                    <div className={classes.iconBox}>
                        <VerifiedUserIcon className={`${classes.Icon} ${classes.IconColorThree}`} />
                        <div className={classes.iconTitle}>
                            <h5>بهترین کیفیت</h5>
                        </div>
                        <div className={`${classes.afterTitle} ${classes.IconColorThree}`}></div>
                    </div>
                </div>
                <div className={`${classes.threeCol} ${classes.threeColTwo}`}>
                    <div className={classes.iconBox}>
                        <RotateLeftIcon className={`${classes.Icon} ${classes.IconColorFour}`} />
                        <div className={classes.iconTitle}>
                            <h5>3 روز ضمانت بازگشت کالا</h5>
                        </div>
                        <div className={`${classes.afterTitle} ${classes.IconColorFour}`}></div>
                    </div>
                    <div className={classes.iconBox}>
                        <PhoneInTalkIcon className={`${classes.Icon} ${classes.IconColorFive}`} />
                        <div className={classes.iconTitle}>
                            <h5>پاسخ گویی 7 روز هفته</h5>
                        </div>
                        <div className={`${classes.afterTitle} ${classes.IconColorFive}`}></div>
                    </div>
                    <div className={classes.iconBox}>
                        <MapIcon className={`${classes.Icon} ${classes.IconColorSix}`} />
                        <div className={classes.iconTitle}>
                            <h5>ارسال به تمام کشور</h5>
                        </div>
                        <div className={`${classes.afterTitle} ${classes.IconColorSix}`}></div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default IconsSection

