import { makeStyles } from '@mui/styles'
import React, { useEffect, useState } from 'react'
import Axios from 'axios'
import {Button, TextField ,Box, Tab ,tabsClasses, FormControl, InputLabel, Select, MenuItem  } from '@mui/material';
import {TabContext, TabList ,TabPanel } from '@mui/lab';

const useStyles = makeStyles((theme)=>({
    adminpage:{
        margin: "110px auto 0 auto",
        maxWidth: "1300px"
    },
    mainContainer:{
        background: "white",
        height: "auto",
        borderRadius: "10px",
        padding: "20px 15px",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
    },
    adminPageTitle:{
        width: "100%",
        display: "flex",
        '& h2':{
            fontSize: "20px"
        }
    },
    container:{
        display: "flex",
        flexDirection: "column",
        gap: "15px",
        alignItems: "center",
        width: "100%"
    },
    createProductTitle:{
        borderBottom: "1px solid lightgray",
        paddingBottom: "10px",
        '& h2':{
            fontSize: "20px",
            color: theme.palette.secondary.main,
        }

    },
    createproductInputs:{
        display: "flex",
        flexDirection: "column",
        gap: "15px",
        width: "50%",
    },
    inputContainre:{

    },
    mainInput:{
        width: "100%!important",
    },

    tablist:{
        '& .MuiTabs-flexContainer':{
            direction: "rtl!important"

        }
    },

    // Categorys
    showAllCategorys:{
        display: "flex",
        flexDirection: 'column',
        alignItems: "center",
        justifyContent: "center",
        "& h2":{
            fontSize: "20px",
            color: theme.palette.secondary.main,
            borderBottom: "1px solid lightgray",
        }
    },
    createNewCategory:{
        display: "flex",
        flexDirection: 'column',
        alignItems: "center",
        justifyContent: "center",
        "& h2":{
            fontSize: "20px",
            color: theme.palette.secondary.main,
            borderBottom: "1px solid lightgray",
        }
    },
    categoryNameInsShow:{
        display: "flex",
        gap: "5px",
        '& h3':{
            fontSize: "15px",
            border: "1px solid lightgray",
            margin: "15px 3px",
            padding: "5px",
        }
    },
    createCategoryContainer:{
        margin: "25px 3px!important"
    },
    createNewCategoryBtn:{
        margin: "15px 0!important",
        width: "100%"
    },

    // edit main page
    changeMainBanerTitile:{
        '& h3':{

        }
    },
    slideEditInput:{
        width: "100%",
        margin: "10px 5px!important"
    },


    // @media
    [theme.breakpoints.down("md")]:{
        adminpage:{
            margin: "65px auto 0 auto",
            width: "100%",
            maxWidth: "1300px",
        },
        createproductInputs:{
            display: "flex",
            flexDirection: "column",
            gap: "10px",
            width: "100%",
        },
        tabBtn:{
            fontSize: "12px!important",
        },
    }
}))

function Adminpage() {
    const classes = useStyles();


     const [productName, setproductName] = useState('');
     const [productSubname, setproductSubname] = useState('');
     const [productInfo, setproductInfo] = useState('');
     const [productImg, setproductImg] = useState('');
     const [productPrice, setproductPrice] = useState('');
     const [productCategory, setproductCategory] = useState('');


    //  send product to db
     const submitProduct = () => {
        Axios.post('http://localhost:3001/api/insert', {
            productName: productName,
            productSubname: productSubname,
            productInfo: productInfo,
            productImg: productImg,
            productPrice: productPrice,
            productCategory: productCategory,
        }).then(() => {
            alert("تغییرات با موفقیت ذخیره شد")
        })
     }



    //  tabs
     const [value, setValue] = React.useState('1');
     const handleChange = (event, newValue) => {
       setValue(newValue);
     };

    //  get categorys from db

     const [categroyList, setcategroyList] = useState([]);
     const [categroyName, setcategroyName] = useState('');

     useEffect(()=>{
         Axios.get("http://localhost:3001/api/getcategory")
         .then((response)=>{
             setcategroyList(response.data)
            })
        },[])

        // send category to db
        const submitCategory = () =>{
            Axios.post('http://localhost:3001/api/insertcategory', {
                categroyName: categroyName,
            }).then(()=>{
                alert("دسته با موفقیت ذخیره شد")
            })
        }
        // send mainbaner to dob
        const submitMainBaner = () =>{
            Axios.post('http://localhost:3001/api/insertcategory', {

            }).then(()=>{
                alert("دسته با موفقیت ذخیره شد")
            })
        }
    return (
        <div className={classes.adminpage}>
            <div className={classes.mainContainer}>
                <div className={classes.adminPageTitle}>
                    <h2>پنل مدیریت</h2>
                </div>
                <Box sx={{ width: '100%', typography: 'body1' }}>
                    <TabContext value={value}>
                        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                            <TabList  className={classes.tablist} variant="scrollable" sx={{[`& .${tabsClasses.scrollButtons}`]: {'&.Mui-disabled': { opacity: 0.3 }, }, }} scrollButtons allowScrollButtonsMobile indicatorColor="secondary" textColor="secondary" onChange={handleChange} aria-label="tabs">
                                <Tab className={classes.tabBtn} label="وضعیت کلی" value="1" />
                                <Tab className={classes.tabBtn} label="ایجاد محصول جدید" value="2" />
                                <Tab className={classes.tabBtn} label="دسته ها" value="3" />
                                <Tab className={classes.tabBtn} label="همه محصولات" value="4"/>
                                <Tab className={classes.tabBtn} label="ویرایش صفحه اصلی" value="5"/>
                                <Tab className={classes.tabBtn} label="تصاویر" value="6"/>
                            </TabList>
                        </Box>
                        <TabPanel value="1">Item One</TabPanel>
                        <TabPanel value="2">
                        <div className={classes.container}>
                            <div className={classes.createProductTitle}>
                                <h2>ایجاد محصول جدید</h2>
                            </div>
                            <div className={classes.createproductInputs}>
                                <div className={classes.inputContainre}>
                                    {/* <label >نام محصول : </label> */}
                                    <TextField key={value.id} label="نام محصول" color="secondary" variant="outlined"  className={classes.mainInput} type="text" name="productName"    onChange={(e)=> {setproductName(e.target.value)}} />
                                </div>
                                <div className={classes.inputContainre}>
                                    {/* <label>مشخصات زیر نام : </label> */}
                                    <TextField key={value.id} label="مشخصات زیر نام" color="secondary" variant="outlined"  className={classes.mainInput} type="text" name="productSubName" onChange={(e)=> {setproductSubname(e.target.value)}}/>
                                </div>
                                <div className={classes.inputContainre}>
                                    {/* <label>توضیحات : </label> */}
                                    <TextField label="توضیحات" color="secondary" variant="outlined"  className={classes.mainInput} type="text" name="productInfo"    onChange={(e)=> {setproductInfo(e.target.value)}}/>
                                </div>
                                    {/* <label>آدرس تصویر : </label> */}
                                    <TextField key={value.id} label="آدرس تصویر" color="secondary" variant="outlined"  className={classes.mainInput} type="text" accept="image/png, image/jpeg" name="productImg"     onChange={(e)=> {setproductImg(e.target.value)}}/>
                                <div className={classes.inputContainre}>
                                    {/* <label>قیمت : </label> */}
                                    <TextField key={value.id} label="قیمت" color="secondary" variant="outlined" className={classes.mainInput}  type="number" name="productPrice"   onChange={(e)=> {setproductPrice(e.target.value)}}/>
                                </div>
                                <div className={classes.inputContainre}>
                                    {/* <label>دسته : </label> */}
                                    <FormControl fullWidth>
                                    <InputLabel id="selectCategoryLabel">انتخاب دسته</InputLabel>
                                    <Select labelId="selectCategoryLabel" id="selectCategory" label="انتخاب دسته" onChange={(e)=> {setproductCategory(e.target.value)}}>
                                        {categroyList.map((value)=>{
                                            return(
                                                <MenuItem key={value.id} value={value.categoryName}>{value.categoryName}</MenuItem>
                                            )
                                        })}
                                    </Select>
                                    </FormControl>
                                </div>

                                <Button onClick={submitProduct} variant="contained" color="secondary">
                                    ثبت
                                </Button>

                            </div>
                        </div>
                        </TabPanel>
                        <TabPanel value="3">
                            <div className={classes.container}>
                                <div className={classes.showAllCategorys}>
                                    <h2>همه دسته ها</h2>
                                    <div className={classes.categorysContainer}>
                                        <div className={classes.categoryNameInsShow}>
                                            {categroyList.map((value)=>{
                                                return(
                                                    <h3 key={value.id}>{value.categoryName}</h3>
                                                )
                                            })}
                                        </div>
                                    </div>
                                </div>
                                <div className={classes.createNewCategory}>
                                    <h2>ساخت دسته جدید</h2>
                                    <div className={classes.createCategoryContainer}>
                                        <TextField label="نام دسته جدید" color="secondary" variant="outlined"  className={classes.mainInput} type="text" name="categoryName" onChange={(e)=> {setcategroyName(e.target.value)}}/>
                                        <Button onClick={submitCategory} variant="contained" className={classes.createNewCategoryBtn}  color="secondary">
                                            ثیت
                                        </Button>
                                    </div>
                                </div>
                            </div>
                        </TabPanel>
                        <TabPanel value="4">Item 4</TabPanel>
                        <TabPanel value="5">
                            <div className={classes.container}>
                                <div className={classes.changeMainBaner}>
                                    <div className={classes.changeMainBanerTitile}>
                                        <h3>ویرایش بنر اصلی سایت</h3>
                                    </div>
                                    <div className={classes.sliderOne}>
                                        <div className={classes.changeSliderOneTitle}>
                                            ویرایش اسلاید اول
                                        </div>
                                        <TextField label="دسته" color="secondary" variant="outlined"  className={classes.slideEditInput} type="text" name="productInfo"    onChange={(e)=> {setproductInfo(e.target.value)}}/>
                                        <TextField label="رنگ" color="secondary" variant="outlined"  className={classes.slideEditInput} type="text" name="productInfo"    onChange={(e)=> {setproductInfo(e.target.value)}}/>
                                        <Button onClick={submitCategory} variant="contained" className={classes.createNewCategoryBtn}  color="secondary">
                                            ثیت
                                        </Button>
                                    </div>
                                </div>
                            </div>
                        </TabPanel>
                        <TabPanel value="6">Item 6</TabPanel>
                    </TabContext>
                </Box>
            </div>
        </div>
    )
}

export default Adminpage
