import React from 'react'

import { makeStyles } from '@mui/styles';
import { Button } from '@mui/material';


const useStyles = makeStyles((theme)=>({
    shopingCartPage:{
        marginTop: "110px",
        maxWidth: "1300px",
        padding: "5px 10px",
        margin: "0 auto",
    },
    shopingCartContainer:{
        background: "#fff",
        padding: "10px",
        borderRadius: "8px",
        maxHeight: "800px",
        overflowX: "hidden",
        overflowY: "scroll",
        margin: "0 auto"
    },
    GoTopaymentContainer:{
        marginRight: "10px",
        paddingTop: "8px",
        paddingBottom: "15px",
        borderBottom: "1px solid lightgray",
        display: "flex",
        alignItems: 'center',
        gap: "15px",

    },
    finalPrice:{
        display: "flex",
        gap: "5px",
    },
    shopingCartTitle:{
        marginRight: "10px",
        paddingTop: "8px",
        paddingBottom: "15px",
        borderBottom: "1px solid lightgray",
        '& h4':{
            fontSize: "16px"
        }
    },
    ShopingCart:{
        width: "100%",
        margin: "10px 0",
        display: "flex",
        flexDirection: "column",
    },
    cart:{
        height: "120px",
        width: "100%",
        margin: "8px",
        border: "1px solid lightgray",
        borderRadius: "8px",

    },
    product:{
        width: "100%",
        height: "100%",
        display: "flex",
        flexDirection: "row-reverse",
    },
    productImage:{
        height: "100%",
        '& img':{
            padding: "5px",
            height: "100%",
        }
    },
    productDetails:{
        display: "flex",
        flexDirection: "row-reverse",
        alignItems: "center",
        gap: "20px",
        width: "85%",
    },
    productIfno:{
        display: "flex",
        flexDirection: "row-reverse",
        alignItems: "center",
        height: "100%"
    },
    productTitle:{
        margin: "5px",
        '& h5':{
            fontSize: "16px"
        }
    },
    productSubtitle:{
        margin: "5px",
        '& h5':{
            fontSize: "12px"
        }
    },
    count:{
        margin: "20px"
    },
    productPriceContainer:{
        display: "flex",
        alignItems: "center",
        flex: "1",
        margin: "0 8px",
        height: "100%",

    },
    priceContainer:{
        display: 'flex',
        alignItems: "center",
        margin: "0 15px",
    },
    productPrice:{
        margin: "0 2px",
    },
    productPriceAfter:{
        margin: "0 2px",

    },
    increaseBtn:{
        minWidth: "30px!important",
    },
    decreaseBtn:{
        minWidth: "30px!important",

    },
    
    // @media
    [theme.breakpoints.down("md")]:{
        shopingCartPage:{
            marginTop: "51px",
        },
        
    },
    [theme.breakpoints.down("sm")]:{
        shopingCartPage:{
            marginTop: "51px",
        },
        shopingCartContainer:{
            padding: "2px",
        },
        productIfno:{
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "flex-start"
        },
        productDetails:{
            display: "flex",
            flexDirection: "row-reverse",
            alignItems: "center",
            gap: "2px",
            width: "100%",
        },
        productTitle:{
            margin: "5px",
            '& h5':{
                fontSize: "11px"
            }
        },
        productSubtitle:{
            margin: "5px",
            '& h5':{
                fontSize: "8px"
            }
        },
        productPriceContainer:{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            flex: "1",
            margin: "0 8px",
            justifyContent: "center",
        },
        productPrice:{
            margin: "8px 0",
            '& h5':{
                fontSize: "11px"
            }
        },
        productPriceAfter:{
            margin: "0 2px",
            '& h5':{
                fontSize: "9px"
            }
        },
        count:{
            margin: "10px"
        },
        increaseBtn:{
            minWidth: "10px!important",
            fontSize: "8px!important",
        },
        decreaseBtn:{
            minWidth: "10px!important",
            fontSize: "8px!important",
        },
        productImage:{
            display: "flex",
            alignItems: "center",
            height: "100%",
            '& img':{
                padding: "5px",
                height: "70%",
            }
        },
    }
}))


function ShopingCart() {
    const classes = useStyles();
    return (
        <div className={classes.shopingCartPage}>
            <div className={classes.shopingCartContainer}>
                <div className={classes.shopingCartTitle}>
                    <h4>سبد خرید</h4>
                </div>
                <div className={classes.GoTopaymentContainer}>
                    <Button size="large" variant="contained" color="success" >
                        پرداخت نهایی
                    </Button>
                    <div className={classes.finalPrice}>
                        <h3>مجموع :</h3>
                        <h3>1,700,000</h3>
                    </div>
                </div>
                <div className={classes.ShopingCart}>
                    {/* cart */}
                    <div className={classes.cart}>
                        <div className={classes.product}>
                            <div to="/shopingfa/product" className={classes.productImage}>
                                <img src="/shopingfa/images/product-images/shopingfa-img (32).jpg" />
                            </div>
                            <div className={classes.productDetails}>
                                <div className={classes.productIfno}>
                                    <div className={classes.productTitle}>
                                        <h5>صندلی اداری چرمی</h5>
                                    </div>
                                    <div className={classes.productSubtitle}>
                                        <h5>مدل G-1</h5>
                                    </div>
                                </div>
                                <div className={classes.productPriceContainer}>
                                    <div className={classes.increase}>
                                        <Button variant="contained" size="small" color="info" className={classes.increaseBtn}>
                                            +
                                        </Button>
                                    </div>
                                    <div className={classes.count}>
                                        <h5>1</h5>
                                    </div>
                                    <div className={classes.decrease}>
                                        <Button variant="contained" size="small" color="info" className={classes.decreaseBtn}>
                                            -
                                        </Button>
                                    </div>
                                </div>
                                <div className={classes.priceContainer}>
                                    <div className={classes.productPrice}>
                                        <h5>1,700,000</h5>
                                    </div>
                                    <div className={classes.productPriceAfter}>
                                        <h5>تومان</h5>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    )
}

export default ShopingCart
