import { makeStyles } from '@mui/styles'
import React, { useEffect, useState } from 'react'

import { Link } from 'react-router-dom';

import Axios from 'axios'

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

import 'swiper/swiper.min.css';
import 'swiper/swiper-bundle.css';

// import Swiper core and required modules
import SwiperCore, {
    Autoplay,Navigation,Pagination
  } from 'swiper';
import { Button } from '@mui/material';

// install Swiper modules
SwiperCore.use([Autoplay,Navigation,Pagination]);



const useStyles = makeStyles((theme)=>({
    mainContainer:{
        padding: "5px 0 35px 0",
        marginBottom: "20px",
        background: theme.palette.primary.siteColorLinearFour
    },
    titleContainer:{
        maxWidth: "1300px",
        margin: "0 auto"
    },
    sliderTitle:{
        color: "white",
        margin: "10px 20px  20px 0",
    },
    afterTitle:{
        background: "red",
        height: "3px",
        width: "15%",
        margin: "5px 0",
        background: theme.palette.btnFour.main,
        mixBlendMode: "color-dodge",
    },
    slider:{
        padding: "0 20px",
        margin: "0 auto",
        maxWidth: "1300px",
        '& .swiper-pagination':{
            top: "220px"
        },
    },
    slide:{
    },
    product:{
        background: "#fff",
        height: "280px",
        borderRadius: "5px",
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-between"
    },
    productImage:{
        height: "130px",
        display: "flex",
        justifyContent: "center",
        margin: "5px",
        padding: "5px",
        "& img":{
            height: "130px",

        }
    },
    productDetails:{
        margin:"0 8px",
    },
    productTitle:{
        fontSize: "14px",
        '& h5':{
            margin: "0 8px"
        }
    },
    productSubtitle:{
        marginTop: "8px",
        fontSize: "11px",
        '& h5':{
            margin: "0 8px"
        }
    },
    productPriceContainer:{
        display: "flex",
        alignItems: "flex-end",
        margin: "8px",
        justifyContent: "space-between",

    },
    productPrice:{
        fontSize: "16px",
    },
    productPriceAfter:{
        marginTop: "3px",
        paddingRight: "5px",
        '& h5':{
            color: "white",
            fontWeight: "normal",
            background: theme.palette.btnFour.main,
            fontSize: "11px",
            padding: "3px",
            borderRadius: "5px"

        }

    },
    addToBasketBtn:{
        color: "#fff!important",
        fontSize: "14px!important",
        maxWidth: "5px!important",
        padding: "2px!important",
        minWidth: "40px!important"
    },
    priceContainer:{
        display:"flex",
        alignItems: "center"
    },

    [theme.breakpoints.down("sm")]:{
        addToBasketBtn:{
            fontSize: "12px!important",
            maxWidth: "5px!important",
            padding: "2px!important",
            minWidth: "30px!important"
        },
        productPrice:{
            "& h5":{
                fontSize: "12px",
            }
        },
        productDetails:{
            margin: "0 2px",
        },
        productSubtitle:{
            '& h5':{
                margin: "0 8px"
            }

        },
        productTitle:{
            '& h5':{
                margin: "0 8px"
            }
        }
    }


}))

function ProductLIstOne() {
    const classes = useStyles();


    return (
        <div className={classes.mainContainer}>
            <div className={classes.titleContainer}>
                <div className={classes.sliderTitle}>
                    <h4>موبایل</h4>
                    <div className={classes.afterTitle}></div>
                </div>
            </div>
            {/* -------------- Slider ------------- */}
            <Swiper className={classes.slider} slidesPerView={2} spaceBetween={10} breakpoints={{
            "640": {
                "slidesPerView": 2,
                "spaceBetween": 10
            },
            "768": {
                "slidesPerView": 4,
                "spaceBetween": 15
            },
            "1024": {
                "slidesPerView": 5,
                "spaceBetween": 20
            }
            }}>
                {/*---------- Slide -------- */}
                {/* only 8 items */}

                <SwiperSlide className={classes.slide}>
                        <div className={classes.product}>
                            <Link to="/shopingfa/productmone" className={classes.productImage}>
                                <img src="/shopingfa/images/products/mobile/galexy note20-i1.jpg" alt="sliderImg"/>
                            </Link>
                            <div className={classes.productDetails}>
                                <div className={classes.productTitle}>
                                    <h5>
                                    گوشی موبایل سامسونگ مدل Galaxy Note20 Ultra SM-N985F/DS
                                    </h5>
                                </div>
                                <div className={classes.productSubtitle}>
                                    <h5>
                                    ظرفیت 256 گیگابایت
                                    </h5>
                                </div>
                                <div className={classes.productPriceContainer}>
                                    <div className={classes.addToBasket}>
                                        <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                            +
                                        </Button>
                                    </div>
                                    <div className={classes.priceContainer}>
                                        <div className={classes.productPrice}>
                                            <h5>
                                                ۲۵,۹۹۹,۹۰۰
                                            </h5>
                                        </div>
                                        <div className={classes.productPriceAfter}>
                                            <h5>تومان</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </SwiperSlide>

                <SwiperSlide className={classes.slide}>
                        <div className={classes.product}>
                            <Link to="/shopingfa/productmtwo" className={classes.productImage}>
                                <img src="/shopingfa/images/products/mobile/Galaxy S20 FE SM-G780-i1.jpg" alt="sliderImg"/>
                            </Link>
                            <div className={classes.productDetails}>
                                <div className={classes.productTitle}>
                                    <h5>
                                    گوشی موبایل سامسونگ مدل Galaxy S20 FE SM-G780 
                                    </h5>
                                </div>
                                <div className={classes.productSubtitle}>
                                    <h5>
                                    دو سیم کارت ظرفیت 128 گیگابایت و 8 گیگابایت رم
                                    </h5>
                                </div>
                                <div className={classes.productPriceContainer}>
                                    <div className={classes.addToBasket}>
                                        <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                            +
                                        </Button>
                                    </div>
                                    <div className={classes.priceContainer}>
                                        <div className={classes.productPrice}>
                                            <h5>
                                            ۱۱,۱۹۵,۰۰۰
                                            </h5>
                                        </div>
                                        <div className={classes.productPriceAfter}>
                                            <h5>تومان</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </SwiperSlide>

                <SwiperSlide className={classes.slide}>
                        <div className={classes.product}>
                            <Link to="/shopingfa/productmthree" className={classes.productImage}>
                                <img src="/shopingfa/images/products/mobile/iphone13promaxA2644-i1.jpg" alt="sliderImg"/>
                            </Link>
                            <div className={classes.productDetails}>
                                <div className={classes.productTitle}>
                                    <h5>
                                    گوشی موبایل اپل مدل iPhone 13 Pro Max A2644
                                    </h5>
                                </div>
                                <div className={classes.productSubtitle}>
                                    <h5>
                                    دو سیم‌ کارت ظرفیت 256 
                                    </h5>
                                </div>
                                <div className={classes.productPriceContainer}>
                                    <div className={classes.addToBasket}>
                                        <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                            +
                                        </Button>
                                    </div>
                                    <div className={classes.priceContainer}>
                                        <div className={classes.productPrice}>
                                            <h5>
                                            ۴۴,۲۴۹,۰۰۰
                                            </h5>
                                        </div>
                                        <div className={classes.productPriceAfter}>
                                            <h5>تومان</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </SwiperSlide>

                <SwiperSlide className={classes.slide}>
                        <div className={classes.product}>
                            <Link to="/shopingfa/productmfour" className={classes.productImage}>
                                <img src="/shopingfa/images/products/mobile/NOKIA-G20 TA-1365-i1.jpg" alt="sliderImg"/>
                            </Link>
                            <div className={classes.productDetails}>
                                <div className={classes.productTitle}>
                                    <h5>
                                    گوشی موبایل نوکیا مدل G20 TA-1365
                                    </h5>
                                </div>
                                <div className={classes.productSubtitle}>
                                    <h5>
                                    دو سیم‌کارت ظرفیت 128 گیگابایت و رم 4 گیگابایت
                                    </h5>
                                </div>
                                <div className={classes.productPriceContainer}>
                                    <div className={classes.addToBasket}>
                                        <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                            +
                                        </Button>
                                    </div>
                                    <div className={classes.priceContainer}>
                                        <div className={classes.productPrice}>
                                            <h5>
                                            ۴,۱۹۹,۰۰۰
                                            </h5>
                                        </div>
                                        <div className={classes.productPriceAfter}>
                                            <h5>تومان</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </SwiperSlide>

                <SwiperSlide className={classes.slide}>
                        <div className={classes.product}>
                            <Link to="/shopingfa/productmfive" className={classes.productImage}>
                                <img src="/shopingfa/images/products/mobile/POCO F3 5G M2012K11AG-i1.jpg" alt="sliderImg"/>
                            </Link>
                            <div className={classes.productDetails}>
                                <div className={classes.productTitle}>
                                    <h5>
                                    گوشی موبایل شیائومی مدل POCO F3 5G M2012K11AG
                                    </h5>
                                </div>
                                <div className={classes.productSubtitle}>
                                    <h5>
                                    دو سیم‌ کارت ظرفیت 128 گیگابایت و 6 گیگابایت رم
                                    </h5>
                                </div>
                                <div className={classes.productPriceContainer}>
                                    <div className={classes.addToBasket}>
                                        <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                            +
                                        </Button>
                                    </div>
                                    <div className={classes.priceContainer}>
                                        <div className={classes.productPrice}>
                                            <h5>
                                            ۴,۱۹۹,۰۰۰
                                            </h5>
                                        </div>
                                        <div className={classes.productPriceAfter}>
                                            <h5>تومان</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </SwiperSlide>

            </Swiper>
        </div>
    )
}


export default ProductLIstOne
