import { makeStyles } from '@mui/styles'
import React ,{ useRef, useState,useEffect } from 'react'

import '../../styles/product.css'

import { Link } from 'react-router-dom';

import MapsHomeWorkIcon from '@mui/icons-material/MapsHomeWork';

// url id
import {useParams} from "react-router-dom";



import Axios from 'axios'

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
import 'swiper/swiper.min.css';
import 'swiper/swiper-bundle.css';
// import Swiper core and required modules
import SwiperCore, {
    Autoplay,Navigation,Pagination,Thumbs
  } from 'swiper';
import { Button } from '@mui/material';
// install Swiper modules
SwiperCore.use([Autoplay,Navigation,Pagination,Thumbs]);




const useStyles = makeStyles((theme)=>({
    productPage:{
        marginTop: "100px",
    },
    mainContainer:{
        maxWidth: "1300px",
        margin:"0 auto",
        padding: "10px",
        width: "100%",
        display:"flex",
        flexDirection: "column"
    },
    productSection:{
        width: "100%",
        display:"flex",
        borderBottom: "1px solid lightgray",
    },
    rightPage:{
        width: "50%",
        height: "500px",
        padding: "5px",
    },
    mainProdoctImg:{
        height: "80%",
        width: "100%",
        '& .swiper-button-prev , .swiper-button-next':{
            background: "rgba(124, 124, 124, 0.418)",
            borderRadius: "20%",
            ' &:after':{
                fontSize: "15px",
                padding: "15px 10px",

            }
        }
    },
    minProductImg:{
        height: "20%",
        boxSizing: "border-box",
        padding: "10px 0",
    },

    leftPage:{
        width: "50%",
        height: "500px",
        padding: "10px",
        display: "flex",
        flexDirection: "column"
    },

    productTitle:{
        borderBottom: "1px solid lightgray",
        paddingBottom: "10px",
        '& h1':{
            fontSize: "18px",
            color: theme.palette.btnThree.main
        },
        '& h3':{
            fontSize: "14px"
        }
    },
    productInfo:{
        flex: "1",
        overflow: "auto",
        marginTop: "10px",
        background: "#fff",
        padding: "15px",
        borderRadius: "6px",
        '& span':{
            fontSize: "13px",
            fontWeight: "bold"
        },
        '& h3':{
            fontSize: "12px",
            fontWeight: "normal"
        }

    },
    productShoping:{
    },
    productOwner:{
        display: "flex",
        marginTop: "10px",
        background: "#fff",
        padding: "15px",
        borderRadius: "6px",
        alignItems: "flex-end",
        '& svg':{
            fontSize: "18px",
            color: theme.palette.btnThree.main,
            margin: "5px"
        },
        '& h4':{
            fontSize: "13px",
            margin: "0 5px"
        },
        '& h5':{
            fontSize: "13px",
            margin: "0 5px"
        }
    },
    productPriceContainer:{
        display: "flex",
        justifyContent: "space-between",
        marginTop: "10px",
        background: "#fff",
        padding: "15px",
        borderRadius: "6px",
    },
    price:{
        display: "flex",
        flex:"1",
        alignItems: "center",
        '& h5':{
            margin: "0 5px"
        }
    },
    addToBasketbtn:{
        flex: "1"

    },
    suggestion:{
        marginTop: "10px",
    },
    productSlide:{
        borderRadius: "8px",
        background: "#fff",
        textAlign: "center",
        display: "-webkit-box",
        display: "-ms-flexbox",
        display: "-webkit-flex",
        display: "flex",
        WebkitJustifyContent: "center",
        justifyContent: "center",
        WebkitBoxAlign: "center",
        msFlexAlign: "center",
        WebkitAlignItems: "center",
        alignItems: 'center',
        '& img':{
            display: "block",
            width: "100%",
            height: "100%",
            objectFit: "contain",
        }
    },


    // @media
    [theme.breakpoints.down("md")]:{
        productPage:{
            marginTop:"51px",
        },
    },


    [theme.breakpoints.down("sm")]:{
        productPage:{
            marginTop:"51px",
        },
        productSection:{
            width: "100%",
            display:"flex",
            flexDirection: "column",
            borderBottom: "1px solid lightgray",
        },
        rightPage:{
            width: "100%",
            height: "350px",
            padding: "5px",
        },
        leftPage:{
            width: "100%",
            height: "500px",
            padding: "10px",
            display: "flex",
            flexDirection: "column"
        },
        productOwner:{
            order: "1"
        },
        productPriceContainer:{

            order: "2"
        },
        productInfo:{
            order: "3"
        }
    },



    // suggestion slider
    suggestionSliderMainContainer:{
        padding: "5px 0 35px 0",
        marginBottom: "20px",
        background: theme.palette.primary.siteColorLinearThree
    },
    titleContainer:{
        maxWidth: "1300px",
        margin: "0 auto"
    },
    sliderTitle:{
        color: "white",
        margin: "10px 20px  20px 0",
    },
    afterTitle:{
        background: "red",
        height: "3px",
        width: "15%",
        margin: "5px 0",
        background: theme.palette.btnThree.main,
        mixBlendMode: "color-dodge",
    },
    slider:{
        padding: "0 20px",
        margin: "0 auto",
        maxWidth: "1300px",
        '& .swiper-pagination':{
            top: "220px"
        },
    },
    slide:{
    },
    product:{
        background: "#fff",
        height: "280px",
        borderRadius: "5px",
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-between"
    },
    productImage:{
        height: "130px",
        display: "flex",
        justifyContent: "center",
        margin: "5px",
        padding: "5px",
        "& img":{
            height: "130px",

        }
    },
    productDetails:{
        margin:"0 8px",
    },
    productTitle:{
        fontSize: "14px",
    },
    productSubtitle:{
        marginTop: "8px",
        fontSize: "11px",
    },
    productPriceContainer:{
        display: "flex",
        justifyContent: "flex-end",
        alignItems: "flex-end",
        margin: "8px",
    },
    productPrice:{
        fontSize: "16px",
    },
    productPriceAfter:{
        marginTop: "3px",
        paddingRight: "5px",
        '& h5':{
            color: "white",
            fontWeight: "normal",
            background: theme.palette.btnThree.main,
            fontSize: "11px",
            padding: "3px",
            borderRadius: "5px"

        }

    },

}))

function ProductMtwo() {

    const { id } = useParams();

    // display items from database


    const [thumbsSwiper, setThumbsSwiper] = useState(null);


    const classes = useStyles();
    return (
        <div className={classes.productPage}>
            <div className={classes.mainContainer}>
                <section className={classes.productSection}>
                    <div className={classes.rightPage}>
                        <Swiper className={classes.mainProdoctImg} style={{'--swiper-navigation-color': '#fff','--swiper-pagination-color': '#fff'}} loop={true} spaceBetween={10} navigation={true} thumbs={{ swiper: thumbsSwiper }} >
                                        <SwiperSlide className={classes.productSlide}>
                                            <img src="./images/products/mobile/Galaxy S20 FE SM-G780-i1.jpg"/>
                                        </SwiperSlide>
                                        <SwiperSlide className={classes.productSlide}>
                                            <img src="./images/products/mobile/Galaxy S20 FE SM-G780-i2.jpg"/>
                                        </SwiperSlide>
                                        <SwiperSlide className={classes.productSlide}>
                                            <img src="./images/products/mobile/Galaxy S20 FE SM-G780-i3.jpg"/>
                                        </SwiperSlide>
                                        <SwiperSlide className={classes.productSlide}>
                                            <img src="./images/products/mobile/Galaxy S20 FE SM-G780-i4.jpg"/>
                                        </SwiperSlide>
                        </Swiper>
                        <Swiper  className={`${classes.minProductImg} productSlider`} onSwiper={setThumbsSwiper} loop={true} spaceBetween={5} slidesPerView={4} freeMode={true} watchSlidesProgress={true}>

                                        <SwiperSlide><img src="./images/products/mobile/Galaxy S20 FE SM-G780-i1.jpg" /></SwiperSlide>
                                        <SwiperSlide><img src="./images/products/mobile/Galaxy S20 FE SM-G780-i2.jpg" /></SwiperSlide>
                                        <SwiperSlide><img src="./images/products/mobile/Galaxy S20 FE SM-G780-i3.jpg" /></SwiperSlide>
                                        <SwiperSlide><img src="./images/products/mobile/Galaxy S20 FE SM-G780-i4.jpg" /></SwiperSlide>

                        </Swiper>
                    </div>

                    <div className={classes.leftPage}>
                        <div className={classes.productTitle}>
                            <h1>
                            گوشی موبایل سامسونگ مدل Galaxy S20 FE SM-G780 
                            </h1>
                            <h3>
                            دو سیم کارت ظرفیت 128 گیگابایت و 8 گیگابایت رم
                            </h3>
                        </div>
                        <div className={classes.productInfo}>
                            <span>توضیحات :</span>
                            <h3>
                            در دنیای امروزی ما، گوشی‌های همراه هوشمند یکی از اعضای خانواده‌های ما شده و بسیاری از کارهایی که در روزمره انجام می‌دهیم، الزام به وجود آن‌ها دارد. با پیشرفت تکنولوژی این گوشی‌ها هم روزانه دارای تغییرات و پیشرفت می‌شوند. یکی از کمپانی‌های بزرگ و مطرح در سطح جهانی سامسونگ است که گوشی‌هایی با محبوبیت بالایی تولید می‌کند و سالانه فروش بسیاری از بازار را به‌دلیل کیفیت به خود اختصاص می‌دهد. گوشی موبایل سامسونگ مدل Galaxy S20 FE SM-G780 دو سیم کارت ظرفیت 128 گیگابایت یکی از همان گوشی‌های باکیفیت این برند است که می‌تواند تمامی نیازهای ما مانند وب‌گردی، بازی آفلاین و آنلاین، تماشای فیلم و کلیپ، شبکه‌های اجتماعی، کار با Applications (نرم‌افزارها) و بسیاری دیگر را برطرف نماید.                            </h3>
                        </div>
                        <div className={classes.productShoping}>
                            <div className={classes.productOwner}>
                                <MapsHomeWorkIcon/>
                                <h4>فروشنده :</h4>
                                <h5>شاپینگ فا</h5>
                            </div>
                            <div className={classes.productPriceContainer}>
                                <div className={classes.price}>
                                    <h4>۱۱,۱۹۵,۰۰۰</h4>
                                    <h5>تومان</h5>
                                </div>
                                <Button className={classes.addToBasketbtn} variant="contained" color="secondary">
                                    <h4>افزودن به سبد خرید</h4>
                                </Button>
                            </div>
                        </div>
                    </div>

                </section>
                <div className={classes.suggestion}>
                    <div className={classes.suggestionSliderMainContainer}>
                        <div className={classes.titleContainer}>
                            <div className={classes.sliderTitle}>
                                <h4>پیشنهاد خرید</h4>
                                <div className={classes.afterTitle}></div>
                            </div>
                        </div>
                    {/* -------------- Slider ------------- */}
                    <Swiper className={classes.slider} slidesPerView={2} spaceBetween={10} breakpoints={{ "640": {"slidesPerView": 2, "spaceBetween": 10}, "768": { "slidesPerView": 4, "spaceBetween": 15 }, "1024": { "slidesPerView": 5, "spaceBetween": 20 }}}>
                        {/*---------- Slide -------- */}
                        {/* only 8 items */}

                                        <SwiperSlide className={classes.slide}>
                                            <div className={classes.product}>
                                                <Link to="/shopingfa/productMone" className={classes.productImage}>
                                                    <img  src="./images/products/mobile/galexy note20-i1.jpg" />
                                                </Link>
                                                <div className={classes.productDetails}>
                                                    <div className={classes.productTitle}>
                                                        <h5>
                                                        گوشی موبایل سامسونگ مدل Galaxy Note20 Ultra SM-N985F/DS
                                                        </h5>
                                                    </div>
                                                    <div className={classes.productSubtitle}>
                                                        <h5>
                                                        ظرفیت 256 گیگابایت

                                                        </h5>
                                                    </div>
                                                    <div className={classes.productPriceContainer}>
                                                        <div className={classes.productPrice}>
                                                            <h5>۲۵,۹۹۹,۹۰۰</h5>
                                                        </div>
                                                        <div className={classes.productPriceAfter}>
                                                            <h5>تومان</h5>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </SwiperSlide>

                                        {/* <SwiperSlide className={classes.slide}>
                                            <div className={classes.product}>
                                                <Link to="/shopingfa/productMtwo" className={classes.productImage}>
                                                    <img  src="./images/products/mobile/Galaxy S20 FE SM-G780-i1.jpg" />
                                                </Link>
                                                <div className={classes.productDetails}>
                                                    <div className={classes.productTitle}>
                                                        <h5>
                                                        گوشی موبایل سامسونگ مدل Galaxy S20 FE SM-G780                                                        </h5>
                                                    </div>
                                                    <div className={classes.productSubtitle}>
                                                        <h5>
                                                        دو سیم کارت ظرفیت 128 گیگابایت و 8 گیگابایت رم

                                                        </h5>
                                                    </div>
                                                    <div className={classes.productPriceContainer}>
                                                        <div className={classes.productPrice}>
                                                            <h5>۱۱,۱۹۵,۰۰۰</h5>
                                                        </div>
                                                        <div className={classes.productPriceAfter}>
                                                            <h5>تومان</h5>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </SwiperSlide> */}

                                        <SwiperSlide className={classes.slide}>
                                            <div className={classes.product}>
                                                <Link to="/shopingfa/productMthree" className={classes.productImage}>
                                                    <img  src="./images/products/mobile/iphone13promaxA2644-i1.jpg" />
                                                </Link>
                                                <div className={classes.productDetails}>
                                                    <div className={classes.productTitle}>
                                                        <h5>
                                                        گوشی موبایل اپل مدل iPhone 13 Pro Max A2644

                                                         </h5>
                                                    </div>
                                                    <div className={classes.productSubtitle}>
                                                        <h5>
                                                        دو سیم‌ کارت ظرفیت 256

                                                        </h5>
                                                    </div>
                                                    <div className={classes.productPriceContainer}>
                                                        <div className={classes.productPrice}>
                                                            <h5>۴۴,۲۴۹,۰۰۰</h5>
                                                        </div>
                                                        <div className={classes.productPriceAfter}>
                                                            <h5>تومان</h5>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </SwiperSlide>

                                        <SwiperSlide className={classes.slide}>
                                            <div className={classes.product}>
                                                <Link to="/shopingfa/productMfour" className={classes.productImage}>
                                                    <img  src="./images/products/mobile/NOKIA-G20 TA-1365-i1.jpg" />
                                                </Link>
                                                <div className={classes.productDetails}>
                                                    <div className={classes.productTitle}>
                                                        <h5>
                                                        گوشی موبایل نوکیا مدل G20 TA-1365

                                                         </h5>
                                                    </div>
                                                    <div className={classes.productSubtitle}>
                                                        <h5>
                                                        دو سیم‌کارت ظرفیت 128 گیگابایت و رم 4 گیگابایت

                                                        </h5>
                                                    </div>
                                                    <div className={classes.productPriceContainer}>
                                                        <div className={classes.productPrice}>
                                                            <h5>
                                                                ۴,۱۹۹,۰۰۰
                                                            </h5>
                                                        </div>
                                                        <div className={classes.productPriceAfter}>
                                                            <h5>تومان</h5>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </SwiperSlide>

                                        <SwiperSlide className={classes.slide}>
                                            <div className={classes.product}>
                                                <Link to="/shopingfa/productMfive" className={classes.productImage}>
                                                    <img  src="./images/products/mobile/POCO F3 5G M2012K11AG-i1.jpg" />
                                                </Link>
                                                <div className={classes.productDetails}>
                                                    <div className={classes.productTitle}>
                                                        <h5>
                                                        گوشی موبایل شیائومی مدل POCO F3 5G M2012K11AG

                                                         </h5>
                                                    </div>
                                                    <div className={classes.productSubtitle}>
                                                        <h5>
                                                        دو سیم‌ کارت ظرفیت 128 گیگابایت و 6 گیگابایت رم

                                                        </h5>
                                                    </div>
                                                    <div className={classes.productPriceContainer}>
                                                        <div className={classes.productPrice}>
                                                            <h5>
                                                            ۴,۱۹۹,۰۰۰
                                                            </h5>
                                                        </div>
                                                        <div className={classes.productPriceAfter}>
                                                            <h5>تومان</h5>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </SwiperSlide>


                    </Swiper>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ProductMtwo
