import { makeStyles } from '@mui/styles'
import React , {useEffect, useState} from 'react'

import { Link } from 'react-router-dom';
import { Button } from '@mui/material';


import Axios from 'axios';
import { useParams } from 'react-router';

const useStyles = makeStyles((theme)=>({
    CategoryPage:{
        marginTop: "110px",
        maxWidth: "1300px",
        padding: "5px 10px",
        margin: "0 auto",
    },
    ListContainer:{
        background: "#fff",
        padding: "10px",
        borderRadius: "8px"
    },
    ListTitle:{
        marginRight: "10px",
        paddingTop: "8px",
        paddingBottom: "15px",
        borderBottom: "1px solid lightgray",
        '& h2':{
            fontSize: "18px"
        }
    },
    categoryList:{
        height: "auto",
        padding: "10px",
    },

    // @media
    [theme.breakpoints.down("md")]:{
        CategoryPage:{
            marginTop:"51px",
        },
    },
    [theme.breakpoints.down("sm")]:{
        CategoryPage:{
            marginTop:"51px",
            maxWidth: "1300px",
            padding: "3px 3px",
            margin: "0 auto",
        },
        ListContainer:{
            padding: "3px",
            borderRadius: "8px"
        },
    },

    // -------------products section
    productmainContainer:{
        marginTop: "10px",
        display: "flex",
        flexWrap: "wrap",
        justifyContent: "center"
    },
    product:{
        margin: "5px",
        height: "340px",
        width: "235px",
        background: "#fff",
        border: '1px solid lightgray',
        borderRadius: "8px",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
    },
    
    productImage:{
        width: "190px",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        overflow: "hidden",
        '& img':{
            padding: "5px",
            width: "95%",
            objectFit: "contain",
        }
        
    },
    productDetails:{
        margin: "5px",
    },
    productTitle:{
        margin: "5px",
        '& h5':{
            fontSize: "15x"
        }
    },
    productSubtitle:{
        marginRight: "5px",
        marginTop: "3px",
        '& h5':{
            fontSize: "12px"
        }
    },
    productPriceContainer:{
        
        display: "flex",
        margin: "10px 5px",
        alignItems: "center",
        justifyContent: "space-between",
    },
    addToBasket:{
        
    },
    addToBasketBtn:{
        color: "#fff!important",
        fontSize: "14px!important",
        maxWidth: "5px!important",
        padding: "2px!important",
        minWidth: "40px!important"
    },
    priceContainer:{
        display:"flex",
        alignItems: "center"
    },
    productPrice:{
        "& h5":{
            fontSize: "16px",
        }
    },
    productPriceAfter:{
        marginTop: "3px",
        paddingRight: "5px",
        '& h5':{
            color: "white",
            fontWeight: "normal",
            background: theme.palette.btnThree.main,
            fontSize: "11px",
            padding: "3px",
            borderRadius: "5px"
        }
    },
    // ----------products section-----  @media
    [theme.breakpoints.down("md")]:{
        product:{
            margin: "2px",
            height: "300px",
            width: "190px",
            background: "#fff",
            border: '1px solid lightgray',
            borderRadius: "8px",
            display: "flex",
        },
        CategoryPage:{
            marginTop: "50px",
        },
    },
    [theme.breakpoints.down("sm")]:{
        mainContainer:{
            marginTop: "4px",
            display: "flex",
            flexWrap: "wrap",
        },
        product:{
            margin: "5px",
            height: "250px",
            width: "150px",
            border: '1px solid lightgray',
            borderRadius: "8px",
        },
        productPrice:{
            "& h5":{
                fontSize: "13px",
            }
        },
        productPriceAfter:{
            marginTop: "3px",
            paddingRight: "5px",
            '& h5':{
                color: "white",
                fontWeight: "normal",
                background: theme.palette.btnThree.main,
                fontSize: "10px",
                padding: "3px",
                borderRadius: "5px"
            }
        },
        addToBasketBtn:{
            fontSize: "12px!important",
            maxWidth: "5px!important",
            padding: "2px!important",
            minWidth: "30px!important"
        },

    }
}))

function CategoryL() {
    const classes = useStyles();


    return (
        <div className={classes.CategoryPage}>
            <div className={classes.ListContainer}>
                <div className={classes.ListTitle}>

                                <h2>لپتاپ</h2>

                </div>
                <div className={classes.categoryList}>
                    <div className={classes.productmainContainer}>

                        <div className={classes.product}>
                                <Link to="/shopingfa/productlone" className={classes.productImage}>
                                    <img src="./images/products/mobile/MacBook Air MGN63 2020-i1.jpg" alt="Img"/>
                                </Link>
                                <div className={classes.productDetails}>
                                    <div className={classes.productTitle}>
                                        <h5>
                                        لپ تاپ 13 اینچی اپل مدل MacBook Air MGN63 2020
                                        </h5>
                                    </div>
                                    <div className={classes.productSubtitle}>
                                        <h5>
                                        MacBook Air MGN63 2020
                                        </h5>
                                    </div>
                                    <div className={classes.productPriceContainer}>
                                        <div className={classes.addToBasket}>
                                            <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                                +
                                            </Button>
                                        </div>
                                        <div className={classes.priceContainer}>
                                            <div className={classes.productPrice}>
                                                <h5>
                                                ۲۹,۱۹۰,۰۰۰
                                                </h5>
                                            </div>
                                            <div className={classes.productPriceAfter}>
                                                <h5>تومان</h5>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            
                            <div className={classes.product}>
                                <Link to="/shopingfa/productltwo" className={classes.productImage}>
                                    <img src="./images/products/mobile/L2-1.jpg" alt="sliderImg"/>
                                </Link>
                                <div className={classes.productDetails}>
                                    <div className={classes.productTitle}>
                                        <h5>
                                        لپ تاپ 13 اینچی اپل مدل MacBook Pro MYDA2 2020
                                        </h5>
                                    </div>
                                    <div className={classes.productSubtitle}>
                                        <h5>
                                        MacBook Pro MYDA2 2020 همراه با تاچ بار
                                        </h5>
                                    </div>
                                    <div className={classes.productPriceContainer}>
                                        <div className={classes.addToBasket}>
                                            <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                                +
                                            </Button>
                                        </div>
                                        <div className={classes.priceContainer}>
                                            <div className={classes.productPrice}>
                                                <h5>
                                                ۳۸,۹۹۹,۰۰۰
                                                </h5>
                                            </div>
                                            <div className={classes.productPriceAfter}>
                                                <h5>تومان</h5>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className={classes.product}>
                            <Link to="/shopingfa/productlthree" className={classes.productImage}>
                                <img src="./images/products/mobile/l3-1.jpg" alt="sliderImg"/>
                            </Link>
                            <div className={classes.productDetails}>
                                <div className={classes.productTitle}>
                                    <h5>
                                    لپ تاپ 15 اینچی لنوو مدل legion 5 15IMH05H
                                    </h5>
                                </div>
                                <div className={classes.productSubtitle}>
                                    <h5>
                                    legion 5 15IMH05H
                                    </h5>
                                </div>
                                <div className={classes.productPriceContainer}>
                                    <div className={classes.addToBasket}>
                                        <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                            +
                                        </Button>
                                    </div>
                                    <div className={classes.priceContainer}>
                                            <div className={classes.productPrice}>
                                                <h5>
                                                ۴۰,۹۵۰,۰۰۰
                                                </h5>
                                            </div>
                                            <div className={classes.productPriceAfter}>
                                                <h5>تومان</h5>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className={classes.product}>
                            <Link to="/shopingfa/productlfour" className={classes.productImage}>
                                <img src="./images/products/mobile/l4-1.jpg" alt="sliderImg"/>
                            </Link>
                            <div className={classes.productDetails}>
                                <div className={classes.productTitle}>
                                    <h5>
                                    لپ تاپ 15 اینچی مایکروسافت مدل Surface Book 3- B
                                    </h5>
                                </div>
                                <div className={classes.productSubtitle}>
                                    <h5>
                                     15 اینچی  Surface Book 3- B
                                    </h5>
                                </div>
                                <div className={classes.productPriceContainer}>
                                    <div className={classes.addToBasket}>
                                        <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                            +
                                        </Button>
                                    </div>
                                    <div className={classes.priceContainer}>
                                        <div className={classes.productPrice}>
                                            <h5>
                                            ۶۰,۶۰۰,۰۰۰
                                            </h5>
                                        </div>
                                        <div className={classes.productPriceAfter}>
                                            <h5>تومان</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className={classes.product}>
                            <Link to="/shopingfa/productlfive" className={classes.productImage}>
                                <img src="./images/products/mobile/l5-1.jpg" alt="sliderImg"/>
                            </Link>
                            <div className={classes.productDetails}>
                                <div className={classes.productTitle}>
                                    <h5>
                                    لپ تاپ 15 اینچی اچ پی مدل G7 255 - H
                                    </h5>
                                </div>
                                <div className={classes.productSubtitle}>
                                    <h5>
                                    G7 255 - H
                                    </h5>
                                </div>
                                <div className={classes.productPriceContainer}>
                                    <div className={classes.addToBasket}>
                                        <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                            +
                                        </Button>
                                    </div>
                                    <div className={classes.priceContainer}>
                                        <div className={classes.productPrice}>
                                            <h5>
                                            ۱۷,۴۹۹,۰۰۰
                                            </h5>
                                        </div>
                                        <div className={classes.productPriceAfter}>
                                            <h5>تومان</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>


                        </div>


                </div>
            </div>
        </div>
    )
}

export default CategoryL
