import { Button, Input } from '@mui/material';
import { makeStyles } from '@mui/styles'
import React from 'react'

import InstagramIcon from '@mui/icons-material/Instagram';
import TelegramIcon from '@mui/icons-material/Telegram';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import TwitterIcon from '@mui/icons-material/Twitter';

const useStyles = makeStyles((theme)=>({
    footer: {
        background: theme.palette.background.paper,
        padding: "5px 30px",
        borderTop: "1px solid lightgray",
        margin: "8px"
    },
    container:{
        maxWidth: "1300px",
        margin: "0 auto"
    },
    companyDetails:{
        paddingTop: "8px",
        display: "flex",
        flexDirection: "column",
        borderBottom: "1px solid lightgray",
        paddingBottom: "15px"
    },
    logoAndGoTopbtn:{
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
    },
    logo:{
        fontSize: "13px",
        color: theme.palette.btnTwo.main
    },
    goTopBtn:{
        color: "gray!important",
        borderColor: "gray!important",
        marginTop: "10px!important",
    },
    CompanyPhone:{
        display: "flex",
        marginTop: "10px",
        '& h6':{
            marginLeft: "5px",
        }
    },
    phoneNuber:{
        direction: "ltr",
    },
    FourcolumnsContainer:{
        display: "flex",
        justifyContent: "space-between",
        marginTop: "10px"
    },

    aboutCompanyContainer:{
        display: "flex",
        flexDirection: "column",
        '& h2':{
            fontSize: "15px",
            marginBottom: "5px",
        },
        '& ul':{
            '& li':{
                fontSize: "13px",
                listStyle: "none",
                padding: "3px 0",
                '& a':{
                    color: "gray",
                    textDecoration: "none",
                    '&:hover':{
                        color: "black",

                    }
                }
            }
        }
    },
    userInfoContainer:{
        display: "flex",
        flexDirection: "column",
        '& h2':{
            fontSize: "15px",
            marginBottom: "5px",
        },
        '& ul':{
            '& li':{
                fontSize: "13px",
                listStyle: "none",
                padding: "3px 0",
                '& a':{
                    color: "gray",
                    textDecoration: "none",
                    '&:hover':{
                        color: "black",

                    }
                }
            }
        }
    },
    clientInfoContianer:{
        display: "flex",
        flexDirection: "column",
        '& h2':{
            fontSize: "15px",
            marginBottom: "5px",
        },
        '& ul':{
            '& li':{
                fontSize: "13px",
                listStyle: "none",
                padding: "3px 0",
                '& a':{
                    color: "gray",
                    textDecoration: "none",
                    '&:hover':{
                        color: "black",

                    }
                }
            }
        }
    },
    newsContainer:{
        display: "flex",
        flexDirection: "column",
        '& h2':{
            fontSize: "15px",
            marginBottom: "5px",
        },
        '& h3':{
            fontSize: "12px",
            marginBottom: "5px",
        },
        '& h4':{
            fontSize: "12px",
        }

    },
    soshalIcons:{
        '& a':{
            color: "gray",
            '& svg':{
                fontSize: "20px",
                '&:hover':{
                    color: theme.palette.btnTwo.main,
                    fontSize: "21px",
                }
            }
        }
    },
    twoColumnFooter:{
        width: "44%",
        display: "flex",
        justifyContent: "space-between",
    },
    emailContainer:{
        marginTop: "8px"
    },
    
    // @media
    [theme.breakpoints.down("sm")]:{
        footer: {
            padding: "5px 10px",
            borderTop: "1px solid lightgray",
            margin: "8px"
        },
        FourcolumnsContainer:{
            flexDirection: "column",
        },
        twoColumnFooter:{
            marginTop: "5px",
            width: "100%",
            display: "flex",
            justifyContent: "space-between",
            padding: "5px 0",
            borderBottom: "1px solid lightgray"
        },
        imailInput:{
            width: "80px"
        },
        newsContainer:{
            paddingLeft: "30px"
        },
        userInfoContainer:{
            paddingLeft: "30px"
        },
    }
}))

function Footer() {
    const classes = useStyles();
    return (
        <footer className={classes.footer}>
            <div className={classes.container}>
                <div className={classes.companyDetails}>
                    <div className={classes.logoAndGoTopbtn}>
                        <div className={classes.logo}>
                            <h1>شاپینگ فا</h1>
                        </div>
                        <Button href="#" variant="outlined" className={classes.goTopBtn}>
                            بازگشت به بالا
                        </Button>
                    </div>
                    <div className={classes.CompanyPhone}>
                        <h6>تلفن پشتیبانی :</h6>
                        <h6 className={classes.phoneNuber}>021- 12332112</h6>
                        <h6> | </h6>
                        <h6 className={classes.phoneNuber}>021- 45665445</h6>
                    </div>
                </div>
                <div className={classes.FourcolumnsContainer}>
                    <div className={classes.twoColumnFooter}>
                        <div className={classes.aboutCompanyContainer}>
                            <h2>درباره شاپینگ فا</h2>
                            <ul>
                                <li><a href="#">درباره ما</a></li>
                                <li><a href="#">شرایط و قوانین</a></li>
                                <li><a href="#">تماس با ما</a></li>
                            </ul>
                        </div>
                        <div className={classes.userInfoContainer}>
                            <h2>حساب کاربری</h2>
                            <ul>
                                <li><a href="#">حساب کاربری</a></li>
                                <li><a href="#">پاسخ به پرسش های متداول</a></li>
                                <li><a href="#">خبر نامه</a></li>
                                <li><a href="#">سبد خرید</a></li>
                                <li><a href="#">کد تخفیف</a></li>
                            </ul>
                        </div>
                    </div>
                    <div className={classes.twoColumnFooter}>
                        <div className={classes.clientInfoContianer}>
                            <h2>خدمات مشتریان</h2>
                            <ul>
                                <li><a href="#">تماس با ما</a></li>
                                <li><a href="#">نحوه ثبت سفارش</a></li>
                                <li><a href="#">بازگشت کالا</a></li>
                            </ul>
                        </div>
                        <div className={classes.newsContainer}>
                            <h2>خبر نامه</h2>
                            <h3>با ما همراه باشید</h3>
                            <div className={classes.soshalIcons}>
                                <a href="#"><InstagramIcon/> </a>
                                <a href="#"><TelegramIcon/> </a>
                                <a href="#"><WhatsAppIcon/> </a>
                                <a href="#"><TwitterIcon/> </a>
                            </div>
                            <h4>از آخرین تخفیف ها باخبر شوید</h4>
                            <div className={classes.emailContainer}>
                                <Input  className={classes.imailInput} color="secondary" />
                                <Button size="small" variant="contained" color="inherit" className={classes.submitEmail}>
                                    ثبت
                                </Button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </footer>
    )
}

export default Footer
