import * as React from 'react';
import {Tabs,Tab} from '@mui/material';
import { makeStyles } from '@mui/styles';

import { useEffect, useState } from 'react';
import Axios from 'axios';

import { Link } from 'react-router-dom';

const useStyles = makeStyles((theme)=>({
    mainTabs:{
        // direction: "ltr!important",
    }
}))



export default function NavBarTabs() {
  const classes = useStyles();

  // tabs
  const [value, setValue] = React.useState(2);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };






  return (
    <Tabs className={classes.mainTabs} value={value} to={`/shopingfa/product/${value.id}`} indicatorColor="secondary" textColor="secondary" centered  onChange={handleChange} aria-label="disabled tabs example">


          <Tab value={1} component={Link} to="/shopingfa/categoryh" label="هدفون و هنزفری" />
          <Tab value={3} component={Link} to="/shopingfa/categorys" label="مودم و تجهیزات شبکه" />
          <Tab value={4} component={Link} to="/shopingfa/categoryz" label="تجهیزات ذخیره سازی" />
          <Tab value={0} component={Link} to="/shopingfa/categoryl" label="لپتاپ" />
          <Tab value={2} component={Link} to="/shopingfa/categorym" label="موبایل" />

    </Tabs>
  );
}