import { makeStyles } from '@mui/styles'
import React , {useEffect, useState} from 'react'

import { Link } from 'react-router-dom';
import { Button } from '@mui/material';


import Axios from 'axios';
import { useParams } from 'react-router';

const useStyles = makeStyles((theme)=>({
    CategoryPage:{
        marginTop: "110px",
        maxWidth: "1300px",
        padding: "5px 10px",
        margin: "0 auto",
    },
    ListContainer:{
        background: "#fff",
        padding: "10px",
        borderRadius: "8px"
    },
    ListTitle:{
        marginRight: "10px",
        paddingTop: "8px",
        paddingBottom: "15px",
        borderBottom: "1px solid lightgray",
        '& h2':{
            fontSize: "18px"
        }
    },
    categoryList:{
        height: "auto",
        padding: "10px",
    },

    // @media
    [theme.breakpoints.down("md")]:{
        CategoryPage:{
            marginTop:"51px",
        },
    },
    [theme.breakpoints.down("sm")]:{
        CategoryPage:{
            marginTop:"51px",
            maxWidth: "1300px",
            padding: "3px 3px",
            margin: "0 auto",
        },
        ListContainer:{
            padding: "3px",
            borderRadius: "8px"
        },
    },

    // -------------products section
    productmainContainer:{
        marginTop: "10px",
        display: "flex",
        flexWrap: "wrap",
        justifyContent: "center"
    },
    product:{
        margin: "5px",
        height: "340px",
        width: "235px",
        background: "#fff",
        border: '1px solid lightgray',
        borderRadius: "8px",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
    },
    
    productImage:{
        width: "190px",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        overflow: "hidden",
        '& img':{
            padding: "5px",
            width: "95%",
            objectFit: "contain",
        }
        
    },
    productDetails:{
        margin: "5px",
    },
    productTitle:{
        margin: "5px",
        '& h5':{
            fontSize: "15x"
        }
    },
    productSubtitle:{
        marginRight: "5px",
        marginTop: "3px",
        '& h5':{
            fontSize: "12px"
        }
    },
    productPriceContainer:{
        
        display: "flex",
        margin: "10px 5px",
        alignItems: "center",
        justifyContent: "space-between",
    },
    addToBasket:{
        
    },
    addToBasketBtn:{
        color: "#fff!important",
        fontSize: "14px!important",
        maxWidth: "5px!important",
        padding: "2px!important",
        minWidth: "40px!important"
    },
    priceContainer:{
        display:"flex",
        alignItems: "center"
    },
    productPrice:{
        "& h5":{
            fontSize: "16px",
        }
    },
    productPriceAfter:{
        marginTop: "3px",
        paddingRight: "5px",
        '& h5':{
            color: "white",
            fontWeight: "normal",
            background: theme.palette.btnThree.main,
            fontSize: "11px",
            padding: "3px",
            borderRadius: "5px"
        }
    },
    // ----------products section-----  @media
    [theme.breakpoints.down("md")]:{
        product:{
            margin: "2px",
            height: "300px",
            width: "190px",
            background: "#fff",
            border: '1px solid lightgray',
            borderRadius: "8px",
            display: "flex",
        },
        CategoryPage:{
            marginTop: "50px",
        },
    },
    [theme.breakpoints.down("sm")]:{
        mainContainer:{
            marginTop: "4px",
            display: "flex",
            flexWrap: "wrap",
        },
        product:{
            margin: "5px",
            height: "250px",
            width: "150px",
            border: '1px solid lightgray',
            borderRadius: "8px",
        },
        productPrice:{
            "& h5":{
                fontSize: "13px",
            }
        },
        productPriceAfter:{
            marginTop: "3px",
            paddingRight: "5px",
            '& h5':{
                color: "white",
                fontWeight: "normal",
                background: theme.palette.btnThree.main,
                fontSize: "10px",
                padding: "3px",
                borderRadius: "5px"
            }
        },
        addToBasketBtn:{
            fontSize: "12px!important",
            maxWidth: "5px!important",
            padding: "2px!important",
            minWidth: "30px!important"
        },

    }
}))

function CategoryM() {
    const classes = useStyles();


    return (
        <div className={classes.CategoryPage}>
            <div className={classes.ListContainer}>
                <div className={classes.ListTitle}>

                                <h2>موبایل</h2>

                </div>
                <div className={classes.categoryList}>
                    <div className={classes.productmainContainer}>

                        <div className={classes.product}>
                                <Link to="/shopingfa/productmone" className={classes.productImage}>
                                    <img src="./images/products/mobile/galexy note20-i1.jpg" alt="Img"/>
                                </Link>
                                <div className={classes.productDetails}>
                                    <div className={classes.productTitle}>
                                        <h5>
                                        گوشی موبایل سامسونگ مدل Galaxy Note20 Ultra SM-N985F/DS
                                        </h5>
                                    </div>
                                    <div className={classes.productSubtitle}>
                                        <h5>
                                        ظرفیت 256 گیگابایت
                                        </h5>
                                    </div>
                                    <div className={classes.productPriceContainer}>
                                        <div className={classes.addToBasket}>
                                            <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                                +
                                            </Button>
                                        </div>
                                        <div className={classes.priceContainer}>
                                            <div className={classes.productPrice}>
                                                <h5>
                                                    ۲۵,۹۹۹,۹۰۰
                                                </h5>
                                            </div>
                                            <div className={classes.productPriceAfter}>
                                                <h5>تومان</h5>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            
                            <div className={classes.product}>
                                <Link to="/shopingfa/productmtwo" className={classes.productImage}>
                                    <img src="./images/products/mobile/Galaxy S20 FE SM-G780-i1.jpg" alt="sliderImg"/>
                                </Link>
                                <div className={classes.productDetails}>
                                    <div className={classes.productTitle}>
                                        <h5>
                                        گوشی موبایل سامسونگ مدل Galaxy S20 FE SM-G780 
                                        </h5>
                                    </div>
                                    <div className={classes.productSubtitle}>
                                        <h5>
                                        دو سیم کارت ظرفیت 128 گیگابایت و 8 گیگابایت رم
                                        </h5>
                                    </div>
                                    <div className={classes.productPriceContainer}>
                                        <div className={classes.addToBasket}>
                                            <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                                +
                                            </Button>
                                        </div>
                                        <div className={classes.priceContainer}>
                                            <div className={classes.productPrice}>
                                                <h5>
                                                ۱۱,۱۹۵,۰۰۰
                                                </h5>
                                            </div>
                                            <div className={classes.productPriceAfter}>
                                                <h5>تومان</h5>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className={classes.product}>
                            <Link to="/shopingfa/productmthree" className={classes.productImage}>
                                <img src="./images/products/mobile/iphone13promaxA2644-i1.jpg" alt="sliderImg"/>
                            </Link>
                            <div className={classes.productDetails}>
                                <div className={classes.productTitle}>
                                    <h5>
                                    گوشی موبایل اپل مدل iPhone 13 Pro Max A2644
                                    </h5>
                                </div>
                                <div className={classes.productSubtitle}>
                                    <h5>
                                    دو سیم‌ کارت ظرفیت 256 
                                    </h5>
                                </div>
                                <div className={classes.productPriceContainer}>
                                    <div className={classes.addToBasket}>
                                        <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                            +
                                        </Button>
                                    </div>
                                    <div className={classes.priceContainer}>
                                            <div className={classes.productPrice}>
                                                <h5>
                                                ۴۴,۲۴۹,۰۰۰
                                                </h5>
                                            </div>
                                            <div className={classes.productPriceAfter}>
                                                <h5>تومان</h5>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className={classes.product}>
                            <Link to="/shopingfa/productmfour" className={classes.productImage}>
                                <img src="./images/products/mobile/NOKIA-G20 TA-1365-i1.jpg" alt="sliderImg"/>
                            </Link>
                            <div className={classes.productDetails}>
                                <div className={classes.productTitle}>
                                    <h5>
                                    گوشی موبایل نوکیا مدل G20 TA-1365
                                    </h5>
                                </div>
                                <div className={classes.productSubtitle}>
                                    <h5>
                                    دو سیم‌کارت ظرفیت 128 گیگابایت و رم 4 گیگابایت
                                    </h5>
                                </div>
                                <div className={classes.productPriceContainer}>
                                    <div className={classes.addToBasket}>
                                        <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                            +
                                        </Button>
                                    </div>
                                    <div className={classes.priceContainer}>
                                        <div className={classes.productPrice}>
                                            <h5>
                                            ۴,۱۹۹,۰۰۰
                                            </h5>
                                        </div>
                                        <div className={classes.productPriceAfter}>
                                            <h5>تومان</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className={classes.product}>
                            <Link to="/shopingfa/productmfive" className={classes.productImage}>
                                <img src="./images/products/mobile/POCO F3 5G M2012K11AG-i1.jpg" alt="sliderImg"/>
                            </Link>
                            <div className={classes.productDetails}>
                                <div className={classes.productTitle}>
                                    <h5>
                                    گوشی موبایل شیائومی مدل POCO F3 5G M2012K11AG
                                    </h5>
                                </div>
                                <div className={classes.productSubtitle}>
                                    <h5>
                                    دو سیم‌ کارت ظرفیت 128 گیگابایت و 6 گیگابایت رم
                                    </h5>
                                </div>
                                <div className={classes.productPriceContainer}>
                                    <div className={classes.addToBasket}>
                                        <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                            +
                                        </Button>
                                    </div>
                                    <div className={classes.priceContainer}>
                                        <div className={classes.productPrice}>
                                            <h5>
                                            ۴,۱۹۹,۰۰۰
                                            </h5>
                                        </div>
                                        <div className={classes.productPriceAfter}>
                                            <h5>تومان</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>


                        </div>


                </div>
            </div>
        </div>
    )
}

export default CategoryM
